import { ImageResponse } from "next/og";
import { getPostBySlug } from "../../../../lib/api";

export const runtime = "edge";

export const alt = "Blog Post";
export const size = {
  width: 1200,
  height: 630, // Standard Twitter card size
};
export const contentType = "image/png";

// Font loading
const interRegular = fetch(
  new URL("../../../../public/fonts/Inter-Regular.woff2", import.meta.url)
).then((res) => res.arrayBuffer());

const interBold = fetch(
  new URL("../../../../public/fonts/Inter-Bold.woff2", import.meta.url)
).then((res) => res.arrayBuffer());

export default async function Image({ params }: { params: { slug: string } }) {
  try {
    const post = await getPostBySlug(params.slug);
    const [interRegularData, interBoldData] = await Promise.all([
      interRegular,
      interBold,
    ]);

    return new ImageResponse(
      (
        <div
          style={{
            background: "#000",
            width: "100%",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            position: "relative",
          }}
        >
          {/* Background Image with Overlay */}
          <div
            style={{
              position: "absolute",
              inset: 0,
              display: "flex",
              background: `url(${post.coverImage}) center/cover no-repeat`,
            }}
          >
            {/* Dark Overlay */}
            <div
              style={{
                position: "absolute",
                inset: 0,
                background: "rgba(0, 0, 0, 0.7)",
              }}
            />
          </div>

          {/* Content */}
          <div
            style={{
              position: "relative",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              textAlign: "center",
              padding: "40px",
              color: "white",
              gap: "20px",
            }}
          >
            {/* Title */}
            <h1
              style={{
                fontSize: "60px",
                fontWeight: "bold",
                letterSpacing: "-0.05em",
                lineHeight: 1.2,
                margin: 0,
                textShadow: "0 2px 4px rgba(0,0,0,0.5)",
              }}
            >
              {post.title}
            </h1>

            {/* Subtitle/Description */}
            <p
              style={{
                fontSize: "32px",
                margin: 0,
                color: "#E5E7EB",
                textShadow: "0 2px 4px rgba(0,0,0,0.5)",
              }}
            >
              {post.excerpt}
            </p>

            {/* Author and Website Info */}
            <div
              style={{
                position: "absolute",
                bottom: "40px",
                display: "flex",
                alignItems: "center",
                gap: "8px",
              }}
            >
              <span
                style={{
                  fontSize: "24px",
                  color: "#9CA3AF",
                }}
              >
                By Dhanush Vardhan • @geeky_dan
              </span>
            </div>
          </div>
        </div>
      ),
      {
        ...size,
        fonts: [
          {
            name: "Inter",
            data: interRegularData,
            weight: 400,
            style: "normal",
          },
          {
            name: "Inter",
            data: interBoldData,
            weight: 700,
            style: "normal",
          },
        ],
      }
    );
  } catch (error) {
    console.error("Failed to generate Twitter image:", error);
    return new Response("Failed to generate Twitter image", { status: 500 });
  }
}
